#aside{
  width: $SIDEBAR_WIDTH;height: 100%;background: #151515;position: relative;

  button{
    width: $SIDEBAR_WIDTH;height: $SIDEBAR_WIDTH;
    span{display: block;font-size: 13px;font-weight: 700; line-height:24px; margin-top: 6px;}
    &[disabled]{opacity: .15;}
  }

  .call-menu{
    position: absolute;left: 0;right: 0;top: 50%;transform: translateY(-50%);
    .active{background: $PRIMARY_COLOR;}
  }

  .btn-menu{
    &.active ~ .menu-layer{
      opacity: 1;visibility: visible;
    }
  }
  .menu-layer{
    opacity: 0;visibility: hidden;transition: opacity .3s;
    border: 1px solid #676c72;background: #3f4246;border-radius: 8px;
    position: absolute;right: calc(100% + 1px);top: 15px; padding: 20px 15px;
    > button{width: 80px;height: 80px;position: relative; margin-bottom: 25px;
      &:last-child{margin-bottom:0;}
    }
    [data-is-new="true"]{
      i{
        position: relative;
        &::before{content: '';width: 8px;height: 8px;background: #de2424;border-radius: 50%;position: absolute;right: -4px;top: -4px;}
      }
    }
  }
  @media(min-width: 1025px){
    width: $SIDEBAR_WIDTH_LG;
    button{
      width: $SIDEBAR_WIDTH_LG;height: $SIDEBAR_WIDTH_LG;
    }
  }
}